<template>
  <div>
    <div class="topbar">
      <router-link :to="{name:'root'}"><span>主页</span></router-link>/ 关于
    </div>
    <div class="about">
      <h2>关于CNode</h2>
      <p>是一款 调用 CNode 官方社区提供的 API，仿CNode 官方社区的Vue项目</p>
      <p>利用vue-cli脚手架进行了项目的搭建，使用vue-router进行前端路由的切换及传参，使用watch监听路由的变化，使用axios获取数据，JSON格式化工具对数据整理，webpack打包</p>
      <p>主要技术: Vue CLI + Vue2+ vue Router + Axios + ES6 + Npm + SCSS</p>
      <p>其中：首页目前就做了导航是 <b>全部</b> 的这一块内容，其他内容雷同，没有做</p>
    </div>
  </div>
</template>

<script>
    export default {
        name: "About"
    }
</script>

<style scoped>
  a{
    text-decoration: none;
  }
  .about{
    padding: 20px;
    background: #fff;
  }
  .topbar{
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
    font-size: 14px;
    color: #999;
  }
  span{
    padding: 10px;
    margin: 0;
    background: #f6f6f6;
    border-radius: 3px 3px 0 0;
    color: #80bd01;
  }
  h2{
    margin-top: 0;
    font-size: 24px;
    color: #333;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
  }
  p{
    font-size: 16px;
    line-height: 32px;
  }
</style>





