<template>
  <div>
    <div class="topbar">
      <router-link :to="{name:'root'}"><span>主页</span></router-link>/ API
    </div>
    <div class="api">
      <h2>主题</h2>

      <h4>get /topics 主题首页</h4>
      <div>
        <span>接收 get 参数</span>
        <ul>
          <li>1、page Number 页数</li>
          <li>2、tab String 主题分类。目前有 ask share job good</li>
          <li>3、limit Number 每一页的主题数量</li>
          <li>4、mdrender String 当为 false 时，不渲染。默认为 true，渲染出现的所有 markdown 格式文本</li>
        </ul>
        <span>示例 <a href="/api/v1/topics">/api/v1/topics</a></span>
      </div>


      <h4>get /topic/:id 主题详情</h4>
      <div>
        <span>接收 get 参数</span>
        <ul>
          <li>1、mdrender String 当为 false 时，不渲染。默认为 true，渲染出现的所有 markdown 格式文本。</li>
          <li>2、accesstoken String 当需要知道一个主题是否被特定用户收藏以及对应评论是否被特定用户点赞时，才需要带此参数。会影响返回值中的 is_collect 以及 replies 列表中的 is_uped 值。</li>
        </ul>
        <span>示例 <a href="/api/v1/topic/5433d5e4e737cbe96dcef312">/api/v1/topic/5433d5e4e737cbe96dcef312</a></span>
      </div>

    </div>
  </div>
</template>

<script>
    export default {
        name: "Api"
    }
</script>

<style  scoped>
  ul,li{
    list-style: none;
  }
  a{
    text-decoration: none;
    color: #08c;
  }
  .api{
    padding: 20px;
    background: #fff;
  }
  .api div{
    margin-bottom: 40px;
  }
  .topbar{
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
    font-size: 14px;
    color: #999;
  }
  span{
    padding-top: 10px ;
    margin: 0;
    font-size: 15px;
  }
  li{
    font-size: 14px;
    border: none;
    padding: 0;
    line-height: 24px;
  }
  h2{
    margin-top: 0;
    font-size: 24px;
    color: #333;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
  }
  h4{
    padding-bottom: 2px;
    border-bottom: 1px solid #eee;
    border-bottom: 1px solid #eee;
  }
</style>



